@import "ScssHelpers/helpers-all.scss";

.steak_club_info {
	// padding-top: 4.8rem;
	// padding-bottom: 6.4rem;
}

.steak_club_info__body {
	max-width: 110rem;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 5.8rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}
}

.steak_club_info__content {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
}

.steak_club_info__inner_list {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 5.8rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}
}

.steak_club_info__list {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, flex-start);

		margin: 0 -2.4rem -3rem 0;
	}

	@include bp($point_4 - 1) {
	}
}

.steak_club_info__item {
	@include bp($point_2, min) {
		width: 25%;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
	}

	@include bp($point_4, min) {
		padding: 0 4.2rem 3rem 0;
	}

	@include bp($point_4 - 1) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.steak_club_info__btn {
	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}
}

.steak_club_info__account {
	@include flex_block(row, nowrap, center, center);
}

.steak_club_info__account_text,
.steak_club_info__account_link {
	&:not(:last-child) {
		margin-right: 3px;
	}

	@include bp($point_4, min) {
		font-size: 1.5rem;
		line-height: 1.6;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.72;
	}
}

.steak_club_info__account_link {
	font-weight: 500;
	color: $gold_2;

	color: all 0.3s;
	text-decoration: underline;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $gold_1;
	}
}
