@import "ScssHelpers/helpers-all.scss";

.memberships_tabs {
	background-color: $gray_3;

	@include bp($point_2, min) {
		padding-bottom: 10rem;
	}
}

.memberships_tabs__head {
	max-width: 80.6rem;
	width: 100%;
	margin: 0 auto;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4rem;
		}
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.memberships_tabs__title {
	@include h2;

	text-align: center;
	letter-spacing: -.018em;

	@include bp($point_2, min) {
		line-height: 1;

		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3) {
		line-height: 1.05;

		&:not(:last-child) {
			margin-bottom: .8rem;
		}
	}
}

.memberships_tabs__text {
	@include text;

	font-weight: 400;
	text-align: center;

	@include bp($point_2, min) {
		line-height: 1.5;
	}
	@include bp($point_3) {
		line-height: 1.45;
		letter-spacing: -.016em;
	}
}

.memberships_tabs__wrap {
	position: relative;
}

.memberships_tabs__button {
	z-index: 10;

	@include bp($point_4, min) {
		display: none;
	}

	@include bp($point_4 - 1) {
		position: absolute;
		top: min(18.2rem, 50%);
	}

	&.memberships_tabs__button--prev_mod {
		left: -2.2rem;
	}

	&.memberships_tabs__button--next_mod {
		right: -2.2rem;
	}
}

.memberships_tabs__nav {
	overflow: visible;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 3.4rem;
		}
		@include bp($point_3) {
			margin-bottom: .6rem;
		}
	}

	@include bp($point_4 - 1) {
		position: relative;
		margin-right: calc(var(--gl-indent)* -1);
		margin-left: calc(var(--gl-indent)* -1);
		// margin-left: 9.6rem;

		&:after {
			position: absolute;
			top: 0;
			right: 0;

			display: block;
			width: 4.4rem;
			height: 100%;
			background: linear-gradient(270deg, #f0f2f4 0%, rgba(240,242,244,0) 100%);

			content: '';
		}
	}
}

.memberships_tabs__nav_list {
	text-align: center;

	@include bp($point_2, min) {
		margin: 0 -3.2rem -2rem;
		@include flex_block(row, nowrap, center, center, flex-start);
	}
	@include bp($point_3) {
		// overflow-x: auto;
		white-space: nowrap;
		padding-bottom: 2rem;

		// &::-webkit-scrollbar {
		// 	display: none;
		// }

		// -ms-overflow-style: none;
		// scrollbar-width: none;
	}

	// @include bp($point_4, min) {
	// 	@include flex_block(row, wrap, center, center, flex-start);
	// }

	// @include bp($point_4 - 1) {
	// }
}

.memberships_tabs__nav_item.memberships_tabs__nav_item {
	width: auto;
	align-self: start;

	@include bp($point_2, min) {
		padding: 0 3.2rem 2rem;
	}

	@include bp($point_3) {
		flex-shrink: 0;
		padding: 0 1.1rem;

		// &:not(:first-child) {
		// 	padding-left: 2.3rem;
		// }
	}
}

.memberships_tabs__nav_link {
	position: relative;

	font-weight: 400;
	letter-spacing: .11em;
	line-height: 1.33;

	color: $gray_2;
	transition: color .3s ease;
	text-transform: uppercase;
	cursor: pointer;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
	}

	.memberships_tabs__nav_item:first-child & {
		// padding-left: 75%;
	}

	&:after {
		position: absolute;
		left: -1px;

		width: 100%;
		background-color: $gold_2;
		transition: opacity 0.3s ease;
		opacity: 0;

		content: "";

		@include bp($point_2, min) {
			bottom: -.4rem;
			height: 2px;
		}
		@include bp($point_3) {
			bottom: -.3rem;
			height: 1px;
		}
	}

	&:focus,
	&:hover {
		color: darken($gray_2, 20);
		text-decoration: none;
	}

	.swiper-slide-thumb-active & {
		color: $gold_2;
		font-weight: 500;

		&:after {
			opacity: 1;
		}
	}
}

.memberships_tabs__content {
	background-color: $white;

	@include bp($point_2, min) {
		padding: 4.6rem 6.4rem 7.4rem;
	}
	@include bp($point_3) {
		padding: 2.4rem 2.4rem 3.2rem;
	}
}

// .memberships_tabs__content_in {
// 	&:not(.memberships_tabs__content_in--active_state) {
// 		display: none;
// 	}
// }

.memberships_tabs__content_row {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5.4rem;
		}
		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}
}

.memberships_tabs__content_body {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, flex-start);
	}
	@include bp($point_3) {
		text-align: center;
	}
}

.memberships_tabs__content_column {

	@include bp($point_2, min) {
		&:nth-child(2n - 1) {
			flex-shrink: 0;

			@include bp($point_2, min) {
				padding-right: 2rem;
			}
		}

		&:nth-child(2n - 2) {
			flex-shrink: 0;
			width: 58.2%;
			margin-left: auto;
			padding-top: 2px;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}
}

.memberships_tabs__content_title {
	@include h2;

	@include bp($point_3) {
		line-height: 1.43;
	}
}

.memberships_tabs__content_info {
	font-weight: 500;
	color: $gray_2;
	letter-spacing: 0.12em;
	line-height: 1.33;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}
	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

.memberships_tabs__content_heading {
	@include h6;

	font-weight: 500;

	@include bp($point_2, min) {
		line-height: 1.6;
		letter-spacing: -0.01em;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}
	@include bp($point_3) {
		line-height: 1.25;
		letter-spacing: -0.023em;
		max-width: 77%;
    margin: 0 auto;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.memberships_tabs__content_text {
	@include text;

	@include bp($point_2, min) {
		max-width: 62rem;

		line-height: 1.5;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
	@include bp($point_3) {
		line-height: 1.45;
    max-width: 89%;
    letter-spacing: -0.012em;
    margin: 0 auto;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.memberships_tabs__content_list {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
	@include bp($point_3) {
		max-width: 74%;
    margin: 0 auto;

		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.memberships_tabs__content_item {
	@include text;

	position: relative;

	line-height: 1.25;
	text-align: left;

	@include bp($point_2, min) {
		padding-left: 3.4rem;
	}
	@include bp($point_3) {
		padding-left: 2.6rem;
		letter-spacing: -0.01em;
		line-height: 1.2;
	}

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	&::after {
		position: absolute;
		top: 50%;

		content: "";

		border-radius: 50%;
		background-color: $black;
		transform: translateY(-40%);

		@include bp($point_2, min) {
			left: 1rem;
			width: 0.6rem;
			height: 0.6rem;
		}
		@include bp($point_3) {
			left: 0;
			width: 4px;
			height: 4px;
		}
	}
}

.memberships_tabs__content_link {
	@include bp($point_3) {
		width: 100%;
	}
}

.memberships_tabs__content_slider_title {
	@include h4;

	letter-spacing: -0.016em;
	line-height: 1.14;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}
